@import "../../global.scss";

.about {
	min-height: fit-content;
	margin: 80px 0px;

	.about-container {
		width: 90%;
		margin: auto;

		@include breakpoint(large) {
			width: 50%;
		}
		h1 {
			font-size: 2rem;
			color: $hotpink;
			font-family: "Rockout";
			text-align: center;
		}
		h2 {
			font-size: 1.5rem;
			color: $hotpink;
			font-family: "Brass Mono";
			text-align: center;
		}
		p,
		li {
			color: white;
		}
	}
}
